.input-item {
  @include pie-clearfix;
  margin-bottom: 16px;
  border-bottom: 1px solid $sub-text-color;

  label {
    float: left;
    padding-right: 12px;
    line-height: 44px;
    font-weight: 500;
    color: $main-text-color;

    &.width-2 {
      width: calc(16px * 2 + 14px); }

    &.width-3 {
      width: calc(16px * 3 + 14px); }

    &.width-4 {
      width: calc(16px * 4 + 14px); }

    &.width-5 {
      width: calc(16px * 5 + 14px); }

    &.width-6 {
      width: calc(16px * 6 + 14px); } }

  .input {
    display: block;
    overflow: hidden;

    &.select {
      background: resolve('icon_44_down_arrow.svg') top right no-repeat; }

    input[type='text'], input[type='password'], input[type='number'], input[type='file'] {
      width: 100%;
      padding: 10px 0;
      line-height: 24px;
      border: 0;
      outline: 0;
      background-color: transparent;

      &.center {
        text-align: center; } }

    select {
      width: 100%;
      height: 44px;
      line-height: 24px;
      border: 0;
      outline: 0;
      background-color: transparent;
      background-image: none;
      box-shadow: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;

      &:focus {
        outline: 0; } } } }

button {
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-weight: 500;
  color: $main-color;
  background-color: white;
  border-radius: 3px;
  border: 2px solid $main-color;
  outline: 0;
  transition: color .25s, background .25s, border .25s;

  &:hover {
    color: lighten($main-color, 5%);
    border-color: lighten($main-color, 5%); }

  &.primary {
    color: white;
    background-color: $main-color;

    &:hover {
      background-color: lighten($main-color, 5%);
      border-color: lighten($main-color, 5%); } } }

.input-item + button {
  margin-top: 24px; }
