*, *:before, *:after {
  box-sizing: border-box; }

body {
  @extend %system-font;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  color: $color-black;
  background-color: $background-color;

  &:before {
    top: auto;
    bottom: 0; } }

img {
  max-width: 100%; }
