.page-header {
  padding: 40px 16px;
  color: white;
  text-align: center;
  background: resolve('page-header-bg.jpg') no-repeat;
  background-size: cover;

  .page-title {
    margin: 0;
    font-size: 24px;
    font-weight: 500; }

  .page-sub-title {
    display: block;
    font-size: 18px;
    font-weight: 500;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } }

  @include tablet {
    padding: 80px 24px;

    .page-title {
      font-size: 30px; } } }
